import { Switch, Route, Redirect } from 'react-router-dom';
import LazyLoad from '@/utils/lazyload';
export default class AdminIndex extends Component {
	
	render() {
		
		return (
			<div className="indexPage" style={{ width: '97%', minHeight: '80%', margin: 0 }}>
				<div className="indexRight">
					<Switch>
						<Route
							path="/index/admin/goodsadmin"
							exact
							render={() => <Redirect to="/index/admin/goodsadmin/home"> </Redirect>}
						/>
						<Route path="/index/admin/goodsadmin/home" component={LazyLoad(() => import('./home'))} />
						<Route
							path="/index/admin/goodsadmin/addorupdategoods"
							component={LazyLoad(() => import('./addOrUpdateGoods'))}
						/>
						{/* <Route path="/index/admin/goodsadmin/updategoods" component={LazyLoad(()=>import('./updateGoods'))}></Route> */}
						<Route
							path="/index/admin/goodsadmin/goodsdetail"
							component={LazyLoad(() => import('./goodsDetail'))}
						/>
					</Switch>
				</div>
			</div>
		);
	}
}
