import './index.scss';
import { Switch, Route, Redirect } from 'react-router-dom';
import LazyLoad from '@/utils/lazyload';
import NavHome from '~/components/nav';
import Head from '~/components/head';
import { Layout } from 'antd';

import AdminIndex from '../admin/goodsAdmin';
import memoryUtils from "@/utils/memoryUtils"

export default class indexPage extends Component {
	// componentWillMount(){
	// 	debugger
		
	// 	debugger
	// }
	render() {
		const user = memoryUtils.user;
		if (!user._id) {
			return <Redirect to="/login" />; // 自动跳转到指定的路由路径
		}
		return (
			<div className="indexPage">
				<div className="empty" />
				<div className="indexLeft">
					<NavHome />
				</div>
				<div className="indexRight">
					<Head />
					<Switch>
						<Route path="/index/admin/goodsadmin" component={AdminIndex} />
						<Route path="/index/admin/typeadmin" component={LazyLoad(() => import('../admin/typeAdmin'))} />
						<Route
							path="/index/chart/histtogram"
							component={LazyLoad(() => import('../chart/histogram'))}
						/>
						<Route path="/index/chart/linechart" component={LazyLoad(() => import('../chart/lineChart'))} />
						<Route path="/index/chart/piechart" component={LazyLoad(() => import('../chart/pieChart'))} />
						<Route path="/index/user" component={LazyLoad(() => import('../user'))} />
						<Route path="/index/role" component={LazyLoad(() => import('../role'))} />
						<Route path="/index/home" component={LazyLoad(() => import('../home'))} />
					</Switch>
				</div>
			</div>
		);
	}
}
