import { withRouter } from 'react-router-dom';
import menuList from '@/config/meun';
import memoryUtils from '@/utils/memoryUtils';
import storageUtils from '@/utils/storageUtils';
import{Modal} from "antd"
import './index.scss';

class Head extends Component {
	getTitle = () => {
		let title = '';
		const path = this.props.location.pathname;
		menuList.forEach((item) => {
			if (path.indexOf(item.key) != -1 && !item.children) {
				title = item.title;
			} else if (item.children) {
				item.children.forEach((cItem) => {
					if (path.indexOf(cItem.key) != -1) {
						title = cItem.title;
					}
				});
			}
		});
		return title;
	};
	esc = () => {
		// 显示确认提示
		Modal.confirm({
			title: '确认退出吗?',
			onOk: () => {
				console.log('OK');
				// 确定后, 删除存储的用户信息
				// local中的
				storageUtils.removeUser();
				// 内存中的
				memoryUtils.user = {};
				// 跳转到登陆界面
				this.props.history.replace('/login');
			},
			onCancel() {
				console.log('Cancel');
			}
		});
	};

	render() {
		const user = memoryUtils.user;
		return (
			<div className="head">
				<div className="headTop">
					欢迎<span> {user.username} </span>
					<span style={{ color: '#bf0000', marginLeft: 10 }} onClick={this.esc}>
						{' '}
						退出{' '}
					</span>
				</div>
				<div className="headBot">
					<div className="title">{this.getTitle()}</div>
					<div />
				</div>
				<div className="san" />
			</div>
		);
	}
}

export default withRouter(Head);
