import './index.scss';
import { Menu, Icon,  } from 'antd';
import menuList from '@/config/meun';
import {Link,withRouter} from "react-router-dom"
import Home from '../../views/home';

const { SubMenu } = Menu;

 class NavHome extends Component {
	getMeun = (menuList) => {
		return menuList.map( item=> {
			if (!item.children) { 
				return (
					<Menu.Item key={item.key}>
						<Link to={item.key}>
							<Icon type={item.icon} />
							<span>{item.title}</span>
						</Link>
					</Menu.Item>
				);
			}else{
				const path = this.props.location.pathname
				if(item.children.find(cItem=>path.indexOf(cItem.key) === 0) ){
						this.openKey = item.key
				}
				return (
					
					<SubMenu
						key={item.key}
						title={
							<span>
								<Icon type={item.icon} />
								<span>{item.title}</span>
							</span>
						}
					>
						{this.getMeun(item.children)}
					</SubMenu>
				);
			}
		
		});
	};
	render() {
		const nowKey = this.props.location.pathname
		const  menuNodes = this.getMeun(menuList)
		return (
			<div>
				<Menu
					onClick={this.handleClick}
					style={{ width: 200, height:"100vh"}}
					selectedKeys={[nowKey]}
					defaultOpenKeys={[this.openKey]}
					mode="inline"
				>
					<div className="top">
						<img src={require('&/image/logo.jpg')} alt="" />
						<span> xx后台 </span>
					</div>

					{menuNodes}
				</Menu>
			</div>
		);
	}
}
export default withRouter(NavHome)
